import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validationKelompok from "../../Validations/validationKelompok";
import { renderField, renderFieldSelect } from "../../renderField";

class FormKelompok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kategori: "",
    };
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="kode_kelompok"
              component={renderFieldSelect}
              options={[
                {
                  value: "PERHIASAN",
                  name: "PERHIASAN",
                },
                {
                  value: "CIOK",
                  name: "CIOK",
                },
                {
                  value: "RONGSOK",
                  name: "RONGSOK",
                },
                {
                  value: "AKSESORIS",
                  name: "AKSESORIS",
                },
              ]}
              type="text"
              label="Kode Kelompok"
              placeholder="Masukan Kode Kelompok"
            />
          </div>
          <div className="col-md-4">
            <Field
              name="kodeKelompok"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Kategori"
            />
          </div>

          <div className="col-md-4">
            <Field
              name="keterangan"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Nama Kategori"
            />
          </div>
          <div className="col-md-12 col-lg-12">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Selesai
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKelompok = reduxForm({
  form: "KelompokTambah",
  validate: validationKelompok,
  enableReinitialize: true,
})(FormKelompok);
export default connect()(FormKelompok);
