import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

import { connect } from "react-redux";
import Swal from "sweetalert2";
import { editKelompok, getListKelompok } from "../actions/kelompokAction";
import { Link } from "react-router-dom";
import FormKelompok from "./Component/FormKelompok";
import { reset } from "redux-form";
import paginationFactory from "react-bootstrap-table2-paginator";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import Skeleton from "react-loading-skeleton";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";
import FormKelompokEdit from "./Component/FormKelompokEdit";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("group/delete/by-kode-group/" + kode)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getListKelompok())
          )
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}
const maptoState = (state) => {
  return {
    listKelompok: state.provinsi.Kelompok,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class Kelompok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Kelompok: [],
      response: "",
      isEdit: false,
      columns: [
        {
          dataField: "kode_kelompok",
          text: "Kode Kelompok",
        },
        {
          dataField: "kode_group",
          text: "Kode Kategori",
        },
        {
          dataField: "nama_group",
          text: "Nama Kategori",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_3"
                  onClick={() => hapus(this.props.dispatch, row.kode_group)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  data-tut="reactour_3"
                  onClick={() => this.showEdit(row)}
                >
                  <i className="fa fa-edit mr-1"></i>
                  Edit
                </button>
              </div>
            );
          },
          headerStyle: () => {
            return { width: "20%" };
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListKelompok());
  }

  showEdit(row) {
    this.setState({
      isEdit: true,
    });
    this.props.dispatch(editKelompok(row));
    this.props.dispatch(showModal());
  }

  showTambah() {
    this.setState({
      isEdit: false,
    });
    this.props.dispatch(showModal());
  }
  handleSubmit(hasil) {
    let data = {
      kode_group: hasil.kodeKelompok,
      nama_group: hasil.keterangan,
      kode_kelompok: hasil.kode_kelompok,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("group/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => this.props.dispatch(getListKelompok()))
          .then(() => this.props.dispatch(reset("KelompokTambah")))
      )
      .catch((err) =>
        this.handleReactive(
          err,
          hasil.kodeKelompok,
          {
            nama_group: hasil.keterangan,
          },
          this.props.dispatch(hideModal()),
          this.props.dispatch(finishSend())
        )
      );
  }
  handleSubmitEdit(hasil) {
    let data = {
      nama_group: hasil.keterangan,
      kode_kelompok: hasil.kode_kelompok,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("group/edit/by-kode-group/" + hasil.kodeKelompok, data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!").then(() =>
          this.props.dispatch(reset("KelompokTambah"))
        )
      )
      .then(() => this.props.dispatch(getListKelompok()))
      .catch((err) =>
        this.handleReactive(
          err,
          hasil.kodeKelompok,
          {
            nama_group: hasil.keterangan,
          },
          this.props.dispatch(hideModal()),
          this.props.dispatch(finishSend())
        )
      );
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("DELETED");
    check
      ? reactive(
          err,
          kode,
          "group/reactive/by-kode-group/",
          data,
          "group/edit/by-kode-group/"
        ).then(() => this.props.dispatch(getListKelompok()))
      : gagal("Data Gagal Ditambahkan");
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiBarang">
            <button
              type="button"
              className="btn btn-secondary"
              data-tut="reactour_4"
            >
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kelompok, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah Data Kelompok yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol navigasi untuk kembali`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Kategori" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <FormKelompokEdit
                        onSubmit={(data) => this.handleSubmitEdit(data)}
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <FormKelompok
                        onSubmit={(data) => this.handleSubmit(data)}
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title="Tambah Data Kategori"
                />
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          data-tut="reactour_1"
                          type="button"
                          className="btn btn-info"
                          onClick={() => this.showTambah()}
                        >
                          Tambah Data
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                <div data-tut="reactour_2">
                  {this.props.listKelompok ? (
                    <ToolkitProvider
                      keyField="kode_kelompok"
                      data={this.props.listKelompok}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right mt-5 mb-3">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <Skeleton width={"100%"} height={180} />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Kelompok);
