const validationKelompok = (value) => {
    const errors = {}
    if (!value.kodeKelompok) {
        errors.kodeKelompok = "Tidak Boleh kosong"
    }
    if (!value.keterangan) {
        errors.keterangan = "Tidak Boleh kosong"
    }
    return errors
}

export default validationKelompok;